"use client";
import React, { useState, useEffect } from "react";
import { Button } from "../../../components/ui/button";

import {
  Table,
  TableHeader,
  TableRow,
  TableHead,
  TableBody,
  TableCell,
} from "../../../components/ui/table";
import { FiEdit, FiTrash2 } from "react-icons/fi";
import {
  Dialog,
  DialogContent,
  DialogFooter,
  DialogHeader,
  DialogTitle,
} from "@/components/ui/dialog";
import { Input } from "@/components/ui/input";

export default function Products() {
  const [products, setProducts] = useState([]);
  const [form, setForm] = useState({
    name: "",
    description: "",
    price: "",
    images: [],
  });
  const [editingProduct, setEditingProduct] = useState(null);
  const [modalOpen, setModalOpen] = useState(false);

  // Mock data for products
  const mockData = [
    {
      _id: "1",
      name: "Product 1",
      description: "Description for Product 1",
      price: 100,
      images: ["image1.jpg"],
    },
    {
      _id: "2",
      name: "Product 2",
      description: "Description for Product 2",
      price: 200,
      images: ["image2.jpg"],
    },
    {
      _id: "3",
      name: "Product 3",
      description: "Description for Product 3",
      price: 300,
      images: ["image3.jpg"],
    },
  ];

  useEffect(() => {
    // Simulating a backend call with mock data
    setProducts(mockData);
  }, []);

  // Open modal for editing product
  const openEditModal = (product) => {
    setEditingProduct(product);
    setForm({
      name: product.name,
      description: product.description,
      price: product.price,
      images: product.images,
    });
    setModalOpen(true);
  };

  // Handle form submit for creating/updating a product
  const handleSubmit = (e) => {
    e.preventDefault();
    // Simulate save/update process
    if (editingProduct) {
      setProducts(
        products.map((p) =>
          p._id === editingProduct._id ? { ...editingProduct, ...form } : p
        )
      );
    } else {
      const newProduct = { ...form, _id: Date.now().toString() }; // Generating unique _id
      setProducts([...products, newProduct]);
    }
    setModalOpen(false);
    setForm({ name: "", description: "", price: "", images: [] });
    setEditingProduct(null);
  };

  // Handle product deletion
  const handleDelete = (id) => {
    setProducts(products.filter((product) => product._id !== id));
  };

  return (
    <div className="p-6 space-y-6">
      <h1 className="text-2xl font-bold mb-4">Admin Panel</h1>

      {/* Button to add new product */}
      <Button
        onClick={() => setModalOpen(true)}
        className="mb-6 bg-blue-600 text-white"
      >
        Add New Product
      </Button>

      {/* Table to display products */}
      <Table className="w-full border">
        <TableHeader>
          <TableRow>
            <TableHead>#</TableHead>
            <TableHead>Name</TableHead>
            <TableHead>Price</TableHead>
            <TableHead>Actions</TableHead>
          </TableRow>
        </TableHeader>
        <TableBody>
          {products.map((product, index) => (
            <TableRow key={product._id}>
              <TableCell>{index + 1}</TableCell>
              <TableCell>{product.name}</TableCell>
              <TableCell>${product.price}</TableCell>
              <TableCell>
                <Button
                  variant="secondary"
                  size="sm"
                  onClick={() => openEditModal(product)}
                  className="bg-yellow-500 text-white mr-2"
                >
                  <FiEdit />
                </Button>
                <Button
                  variant="destructive"
                  size="sm"
                  onClick={() => handleDelete(product._id)}
                  className="bg-red-600 text-white"
                >
                  <FiTrash2 />
                </Button>
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>

      {/* Modal for adding/editing a product */}
      <Dialog open={modalOpen} onOpenChange={setModalOpen}>
        <DialogContent>
          <DialogHeader>
            <DialogTitle>
              {editingProduct ? "Edit Product" : "Add Product"}
            </DialogTitle>
          </DialogHeader>
          <form onSubmit={handleSubmit}>
            <div className="space-y-4">
              <Input
                placeholder="Product Name"
                value={form.name}
                onChange={(e) => setForm({ ...form, name: e.target.value })}
                className="w-full"
              />
              <Input
                placeholder="Description"
                value={form.description}
                onChange={(e) =>
                  setForm({ ...form, description: e.target.value })
                }
                className="w-full"
              />
              <Input
                type="number"
                placeholder="Price"
                value={form.price}
                onChange={(e) => setForm({ ...form, price: e.target.value })}
                className="w-full"
              />
              <Input
                type="file"
                onChange={(e) => setForm({ ...form, images: e.target.files })}
                className="w-full"
              />
            </div>
            <DialogFooter>
              <Button
                variant="secondary"
                onClick={() => {
                  setModalOpen(false);
                  setEditingProduct(null);
                  setForm({ name: "", description: "", price: "", images: [] });
                }}
                className="mr-2"
              >
                Cancel
              </Button>
              <Button type="submit" className="bg-blue-600 text-white">
                {editingProduct ? "Save Changes" : "Add Product"}
              </Button>
            </DialogFooter>
          </form>
        </DialogContent>
      </Dialog>
    </div>
  );
}
